.skills-container{padding:1px 15px;background-color:#FFF}.skills .skill-row{height:40px;line-height:40px;margin:20px 0}.skills .text{font-size:32px;letter-spacing:1px;white-space:nowrap}.skills .text.left{padding-right:10px;float:left}.skills .text.right{padding-left:10px;float:right}.skills .rect{width:100%;height:100%;overflow:hidden;border:3px solid #1d1f20;background-color:#EFF5FC}.skills .rect:nth-child(odd){background:rgba(212,244,249,0.9);background:linear-gradient(135deg, rgba(52,170,255,0.65) 0%, rgba(162,216,255,0.83) 56%, rgba(212,244,249,0.92) 100%)}.skills .rect:nth-child(even){background:#b9e8d6;background:linear-gradient(135deg, rgba(0,171,108,0.75) rgba(185,232,214,0.77) 0%, rgba(138,216,201,0.77) 56%, rgba(0,171,108,0.75) 100%)}@media (min-width: 570px){.skills-container{padding:5px 30px}.skills .text{font-size:42px;letter-spacing:4px;white-space:nowrap}}@media (min-width: 992px){.skills-container{padding:5px 45px}.skills .text{font-size:56px;letter-spacing:5px;white-space:nowrap}}
